 #send-email {
   margin-top: 0px !important;
   margin-bottom: 30px;
}

#send-email .send_email_checkbox {
   padding: 0.5em 1em;
   background-color: #dad9d9;
   display: flex;
   align-items: center;
   grid-gap: 7px;
}

#user-email-address {
   padding: 1em;
   background-color: #eee;
}

.wp-travel-itinerary-downloads {
   text-align: center;
   margin: 30px 0;
}

.wp-travel-itinerary-downloads a.itinerary-download-button {
   background: #dadada;
   padding: 10px 20px;
   font-size: 18px;
   border-radius: 3px;
   text-decoration: none;
   color: #545454 !important;
   transition: .3s ease-in-out linear;
}

#user-email-address {
   padding: 1em;
   background-color: #eee;
}

#user-email-address form#ajax-email-form {
   padding: 0;
   background: none;
   display: flex;
   position: relative;
   grid-gap: 2px;
}

#user-email-address input#email-id {
   padding-left: 15px !important;
}

#user-email-address .form input[type=text]{
   outline: 0;
    background: #fff;
    width: 100%;
    margin: 0 0 15px;
    padding: 5px 15px 5px 50px !important;
    box-sizing: border-box;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
}
#user-email-address strong{
   display: none;
}
#user-email-address button.btn-submit {
   height: 40px;
   text-transform: uppercase;
   outline: 0;
   background: #df1814;
   border: 0;
   padding: 10px 30px;
   color: #fff;
   font-size: 14px;
   transition: all .3s ease;
   cursor: pointer;
   letter-spacing: 3px;
   border-radius: 0;
}